.news {
    padding: 25px 0; }

.news__title {
    @include title; }

.news__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }

.news__item {
    position: relative;
    display: flex;
    flex-direction: column;

    max-width: 272px;
    width: 100%;
    max-height: 500px;
    height: 100%;
    margin-bottom: 25px;

    overflow: hidden;

    transition: all .2s linear;

    box-shadow: 0 0 20px rgba(0, 0, 0, .1); }

.news__item:hover .news__item-img {
    transform: scale(1.05); }

// .news__item:hover .news__item-txt
//     color: $color-primary

.news__item-link {
    width: 100%;
    height: 85%;
    position: absolute;
    top: 0;
    left: 0; }

.news__item-img {
    min-width: 272px;
    max-width: 272px;
    min-height: 153px;
    max-height: 153px;

    z-index: -1;

    transition: transform .3s ease-out;

    object-fit: cover; }

.news__item-content {
    display: flex;
    flex-direction: column;
    padding: 15px;
    color: $color-secondary-dark; }

.news__item-txt {
    font-weight: 600;

    min-height: 100px;
    max-height: 100px;

    overflow: hidden;
    margin-bottom: 20px;

    color: $color-primary-dark;
    transition: color .1s linear; }

.news__item-info {
    display: flex;
    justify-content: space-between;
    font-size: 14px; }

.news-icon {
    height: 15px;
    width: 15px;
    margin-right: 5px;
    fill: $color-secondary-dark; }

.news__item-views-wrap {
    display: flex;
    align-items: center; }
